import React from 'react';

const Company = ({data}) => {
    return (
        <div style={{marginRight: "70px"}}>
            <h3>Company details</h3>
            <p>Name: {data.name}</p>
            <p>Catch Phrase: {data.catchPhrase}</p>
            <p>BS: {data.bs}</p>
            
        </div>
    );
};

export default Company;