import React from 'react';

const SingleUser = ({user, handleShowCompany, handleShowAddress, handleDelete}) => {
    return (
        <tr>
            <td>{user.name}</td>
            <td>{user.username}</td>
            <td>{user.phone}</td>
            <td>{user.website}</td>
            <td>{user.email}</td>
            <td onClick={()=> handleShowCompany(user.company)} className="clickable">{user.company.name}</td>
            <td onClick={()=> handleShowAddress(user.address)} className="clickable">{user.address.street}</td>
            <td onClick={()=> handleDelete(user.id)} className="delete"><i>Delete</i></td>
        </tr>
    );
};

export default SingleUser;