import React from 'react';

const Address = ({data}) => {
    return (
        <div>
            <h3>Address details</h3>
            <p>City: {data.city}</p>
            <p>Geo: {data.geo?.lat}, {data.geo?.lng}</p>
            <p>Street: {data.street}</p>
            <p>Suite: {data.suite}</p>
            <p>Zip code: {data.zipcode}</p>
        </div>
    );
};

export default Address;