import React from 'react';
import SingleUser from '../SingleUser/SingleUser';
import './AllUsers.css'

const AllUsers = ({users, handleShowCompany, handleShowAddress, handleDelete}) => {
    return (
        <div className="container">
            <table>
  <tr>
    <th>Name</th>
    <th>Username</th>
    <th>Phone</th>
    <th>Website</th>
    <th>Email</th>
    <th>Company</th>
    <th>Address</th>
    <th>Delete</th>
  </tr>
  {
      users.map((user)=> (
        <SingleUser
         user={user}
         handleShowCompany={handleShowCompany}
         handleShowAddress={handleShowAddress}
         handleDelete={handleDelete}

         />
      ))
  }
</table>
        </div>
    );
};

export default AllUsers;