import axios from 'axios';
import React, { Component } from 'react';
import AllUsers from './components/AllUsers/AllUsers';
import Company from './components/Company/Company';
import './App.css'
import Address from './components/Address/Address';

class App extends Component {
  constructor(props) {
    super(props);
      this.state = {
        users: [],
        companyData: '',
        addressData: ''
      }
  }

  componentDidMount(){
    axios.get('https://jsonplaceholder.typicode.com/users').then((res) => {
      console.log(res, 'ress')
      this.setState({users: res.data})
    })
  }

 
  render() {

    const handleShowCompany = (data) => {
      console.log(data, "companyData")
      this.setState({companyData: data})
    }
    const handleShowAddress = (data) => {
      console.log(data, "companyData")
      this.setState({addressData: data})
    }

    const handleDelete = (id) => {
      const filteredUsers = this.state.users.filter((user) => user.id !== id)
      this.setState({users: filteredUsers})
    }
    return (
      <div>
        <AllUsers 
        handleShowCompany={handleShowCompany} 
        users={this.state.users}
        handleShowAddress={handleShowAddress}
        handleDelete={handleDelete}
        />
        <div className="componentContainer">
        <Company data={this.state.companyData} />
        <Address data={this.state.addressData} />
        </div>
      </div>
    );
  }
}

export default App;